<template>
  <div>
    <el-dialog
      title="处理"
      :visible.sync="wareHouseFormVisible"
    >
      <span class="dialog-title">平仓信息</span>
      <el-form
        :model="formData"
        :inline="true"
        ref="formData"
      >
        <el-form-item
          label="仓单编号"
          :label-width="formLabelWidth"
          style="width:365px;"
        >
          {{form.wareHouseId}}
        </el-form-item>
      </el-form>
      <el-form
        :model="formData"
        :inline="true"
        :rules="rules"
        ref="formData"
      >
        <el-form-item
          label="交易方式"
          :label-width="formLabelWidth"
          style="width:365px;"
          prop="tradeWay"
        >
          <el-select
            v-model="formData.tradeWay"
            placeholder="请选择交易方式"
            :disabled="(form.status == 1)"
          >
            <el-option
              label="回购"
              value="1"
            ></el-option>
            <el-option
              label="抛售"
              value="2"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item
          label="平仓总价"
          :label-width="formLabelWidth"
          prop="closepositionPrice"
        >
          <el-input
            v-model="formData.closepositionPrice"
            :disabled="(form.status == 1)"
          ></el-input>
        </el-form-item>
        <el-form-item
          label="平仓时间"
          :label-width="formLabelWidth"
          style="width:365px;"
          prop="closepositionTime"
        >
          <el-date-picker
            type="datetime"
            placeholder="选择日期时间"
            v-model="formData.closepositionTime"
            value-format="timestamp"
            style="width: 100%;"
            :disabled="(form.status == 1)"
          ></el-date-picker>
        </el-form-item>
        <el-form-item
          label="平仓时间期货价格"
          :label-width="formLabelWidth"
          prop="closepositionOptionPrice"
        >
          <el-input
            v-model="formData.closepositionOptionPrice"
            :disabled="(form.status == 1)"
          ></el-input>
        </el-form-item>
      </el-form>
      <div class="buttonGroup">
        <el-button
          type="primary"
          @click="liquidate('formData', form.status)"
          v-if="(form.status == 0)"
        >确认平仓</el-button>
        <el-button
          type="primary"
          @click="liquidate('formData', form.status)"
          v-else
        >确认还款并归档</el-button>
        <el-button
          type="primary"
          @click="deleteRecord('合同')"
        >删除合同</el-button>
      </div>
      <div
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="wareHouseFormVisible = false">取 消</el-button>
        <!-- <el-button type="primary" @click="dialogFormVisible = false">确 定</el-button> -->
      </div>
    </el-dialog>

    <el-dialog
      title="处理"
      :visible.sync="warningOperateVisible"
    >
      <el-tabs type="border-card">
        <el-tab-pane label="价值预警">
          <table class="value-warning">
            <tr>
              <th>放款金额</th>
              <td>2</td>
              <!-- <th>&nbsp;</th>
              <td>&nbsp;</td> -->
            </tr>
            <tr>
              <th>仓单原价值</th>
              <td>4</td>
              <th>仓单现价值</th>
              <td>6</td>
            </tr>
            <tr>
              <th>原保证金</th>
              <td>8</td>
              <th>现保证金</th>
              <td>10</td>
            </tr>
          </table>
        </el-tab-pane>
        <el-tab-pane label="保值方案">
          <div class="program">
            需提醒客户追加1000吨的货物或65040000元保证金。
          </div>
        </el-tab-pane>
        <el-tab-pane label="风险处理">
          <div class="program">
            客户实际追加
            <el-input
              v-model="programForm.weight"
              clearable
              style="width:120px"
            >
            </el-input>
            吨的货物，
            <el-input
              v-model="programForm.ammount"
              clearable
              style="width:120px"
            >
            </el-input>
            元保证金。
          </div>
          <el-button
            type="primary"
            class="dialog-button"
          >确定</el-button>
        </el-tab-pane>
      </el-tabs>
    </el-dialog>
  </div>
</template>

<script>
import { rules } from "../validate.js";
import * as ajaxUrl from "../ajax.config.js";


export default {
  props: ["dialogFormVisible", "form", "dialogFormVisible1"],
  data() {
    return {
      formData: {},
      programForm: {
        weight: "",
        ammount: ""
      },

      wareHouseFormVisible: this.dialogFormVisible,
      warningOperateVisible: this.dialogFormVisible1,
      formLabelWidth: "135px",
      rules: rules,

      liquidated: false //判断是否平仓
    };
  },
  watch: {
    dialogFormVisible(val) {
      this.wareHouseFormVisible = val; //新增result的watch，监听变更并同步到myResult上
    },
    dialogFormVisible1(val) {
      this.warningOperateVisible = val;
    },
    wareHouseFormVisible(val) {
      this.$emit("dialogFormVisibleChange", val); //③组件内对dialogFormVisibleChange变更后向外部发送事件通知
    },
    warningOperateVisible(val) {
      this.$emit("warningOperateVisibleChange", val); //③组件内对dialogFormVisibleChange变更后向外部发送事件通知
    }
  },
  methods: {
    liquidate(formData, status) {
      this.$refs[formData].validate(valid => {
        if (valid && status == 0) {
          this.formData.id = this.form.contractId;
          this.formData.status = status + 1;
          console.log(this.formData.status)
          let params=this.$qs.stringify(this.formData);
          this.$confirm("是否平仓?", "提示", {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            type: "warning"
          })
            .then(() => {
              this.axios
                .put(ajaxUrl.HOLDWARRANTCONFIRM+"/"+this.form.id, params)
                .then(res => {
                  this.$message({
                    type: "success",
                    message: "已平仓!"
                  });
                  this.wareHouseFormVisible = false;
                  this.formData = {};
                  // this.$router.push("/GetContract");
                  this.$emit("searchInfo");
                })
                .catch(err => {
                  console.log(err);
                });
            })
            .catch(() => {
              this.$message({
                type: "info",
                message: "已取消平仓"
              });
            });
        } else if (valid && status == 1) {
          this.formData.id = this.form.contractId;
          this.formData.status = status + 1;
          let params=this.$qs.stringify(this.formData);
          this.$confirm("是否归档?", "提示", {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            type: "warning"
          })
            .then(() => {
              this.axios
                .put(ajaxUrl.HOLDWARRANTCONFIRM, params)
                .then(res => {
                  this.$message({
                    type: "success",
                    message: "已归档!"
                  });
                  this.deleteOperateRecord(this.form.id);
                  this.wareHouseFormVisible = false;
                  this.formData = {};
                  this.$router.push("/GetContract");
                  this.$emit("searchInfo");
                })
                .catch(err => {
                  console.log(err);
                });
            })
            .catch(() => {
              this.$message({
                type: "info",
                message: "已取消平仓"
              });
            });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    deleteRecord(type) {
      this.$emit("deleteRecord", type);
    },
    deleteOperateRecord(id){
      this.axios.delete(ajaxUrl.DELETEINDEXINFO, {
        params: {
          id: id
        }
      }).then(res => {
        console.log(res.data.resMsg)
      }).catch(err => {
        console.log(err)
      })
    }
  },
  created() {},
  mounted() {},
  components: {}
};
</script>

<style>
.delete-contract {
  display: block;
  margin-bottom: 15px;
  width: 85px;
}
.delete-contract:hover {
  cursor: pointer;
  border-bottom: 1px solid black;
}
.value-warning {
  border-spacing: 0;
  border-collapse: collapse;
}
.value-warning tr th {
  width: 150px;
  height: 70px;
  border: 1px solid gray;
}
.value-warning tr td {
  width: 200px;
  height: 70px;
  text-align: center;
  border: 1px solid gray;
}
.program {
  height: 100px;
}
.dialog-button {
  float: right;
}
</style>



