<template>
  <div>
    <vSearch
      @getOption="getOption"
      @searchInfo="searchInfo"
      @clearSearchInfo="clearSearchInfo"
      :pageType="pageType"
    ></vSearch>
    <el-table
      :data="tableData"
      style="width: 100%"
      max-height="675"
      @sort-change="sortChange"
    >
      <el-table-column
        prop="date"
        label="操作"
        min-width="45%"
      >
        <template slot-scope="scope">
          <el-button
            @click.native.prevent="operateRow(scope.row)"
            type="text"
            size="small"
          >
            处理
          </el-button>
        </template>
      </el-table-column>
      <el-table-column
        prop="formType"
        label="流程名称"
        :formatter="statusSwitch"
        sortable
      >
      </el-table-column>
      <el-table-column
        prop="customerNo"
        label="客户号"
        min-width="65%"
      >
      </el-table-column>
      <el-table-column
        prop="customerName"
        label="客户名称"
        sortable
      >
      </el-table-column>
      <el-table-column
        prop="contractNo"
        label="合同编号"
        sortable
      >
      </el-table-column>
      <el-table-column
        prop="wareHouseId"
        label="仓单编号(仓单总数)"
        :formatter="wwIdFormatter"
        min-width="120%"
      >
      </el-table-column>
      <el-table-column
        prop="goodsName"
        label="仓单品种"
        sortable
      >
      </el-table-column>
      <el-table-column
        prop="originalPrice"
        label="仓单交易价格"
        sortable
        min-width="100%"
      >
      </el-table-column>
      <el-table-column
        prop="duration"
        label="期限"
        :formatter="durationFormat"
        sortable
      >
      </el-table-column>
      <el-table-column
        prop="tradeEndtime"
        label="交易到期日"
        :formatter="dateFormat"
        sortable
      >
      </el-table-column>
      <!-- <el-table-column
        prop="companyNmae"
        label="发起机构"
        sortable
      >
      </el-table-column> -->
    </el-table>
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-size="pageSize"
        layout="prev, pager, next, jumper"
        :total="totalPage"
      >
      </el-pagination>
    </div>
    <handleDialog
      :dialogFormVisible="dialogFormVisible"
      :dialogFormVisible1="dialogFormVisible1"
      :form="form"
      @dialogFormVisibleChange="dialogFormVisibleChange"
      @warningOperateVisibleChange="warningOperateVisibleChange"
      @deleteRecord="deleteRecord"
    ></handleDialog>
  </div>
</template>

<script>
import vSearch from "../components/Search.vue";
import handleDialog from "../components/HandleDialog.vue";
import * as ajaxUrl from "../ajax.config.js";

export default {
  data() {
    return {
      pageType: 0,
      operateStatus: 0, //0:预警信息 1:持有仓单
      searchForm: {
        option: "",
        inputText: ""
      },
      searchParam:{},
      tableData: [],
      currentPage: 1,
      pageSize: 1,
      totalPage: 100,
      form: {
        wareHouseId: "", //仓单编号
        txAccount: "", //交易帐户
        time: "", //平仓时间
        txType: "", //交易方式
        liquidatePrice: "", //平仓时间期货价格
        liquidateValue: "" //平仓总价
      },
      dialogFormVisible: false,
      dialogFormVisible1: false
    };
  },
  mounted() {
    this.searchInfo();
  },
  methods: {
    getOption(value, inputText) {
      this.searchForm.option = value;
      this.searchForm.inputText = inputText;
    },
    searchInfo(orderField, orderType) {
      this.searchParam[this.searchForm.option] = this.searchForm.inputText; 
      this.axios
        .get(ajaxUrl.INDEXINFO, {
          params: {
            pageSize: this.pageSize,
            pageNum: this.currentPage,
            search: this.searchParam,
            orderField: orderField,
            orderType: orderType
          }
        })
        .then(res => {
          this.tableData = res.data.resData;
          this.totalPage = res.data.page.total;
          this.pageSize = res.data.page.pageSize;
          this.currentPage = res.data.page.current;
          this.searchParam = {};
        })
        .catch(err => {
          console.log(err);
        });
    },
    clearSearchInfo() {
      this.searchForm.option = "";
      this.searchForm.inputText = "";
    },
    formatter(row, column) {
      return row.address;
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
    },
    operateRow(row) {
      this.form = row;
      if (row.formType == "0") {
        this.dialogFormVisible = true;
        this.form.wareHouseId = row.warehouseWarrantList.length > 1 ? row.warehouseWarrantList[0].wwNo+"~"+ row.warehouseWarrantList[row.warehouseWarrantList.length-1].wwNo+"("+row.warehouseWarrantList.length+")":((row.warehouseWarrantList.length==1)?(row.warehouseWarrantList.wwNo):"-");
        this.form.contractId = row.contractNo;
      } else if (row.formType == "1") {
        this.dialogFormVisible1 = true;
      }
    },
    dialogFormVisibleChange(val) {
      this.dialogFormVisible = val;
    },
    warningOperateVisibleChange(val) {
      this.dialogFormVisible1 = val;
    },
    //操作状态处理
    statusSwitch(row, column) {
      switch (row.formType) {
        case "0":
          return "仓单处理";
          break;

        case "1":
          return "预警消息";
          break;
      }
    },
    //
    dateFormat(row, column){
      return this.common.fullTime(row.tradeEndtime)
    },
    durationFormat(row, column){
      return this.common.durationTime(row.tradeEndtime, row.nowdate)
    },
    wwIdFormatter(row, column){
      if(row.warehouseWarrantList.length == 0){
        return "-"
      }else if(row.warehouseWarrantList.length == 1){
        return row.warehouseWarrantList[0].wwNo;
      }else if(row.warehouseWarrantList.length > 1){
        return row.warehouseWarrantList[0].wwNo+"~"+ row.warehouseWarrantList[row.warehouseWarrantList.length-1].wwNo+"("+row.warehouseWarrantList.length+")"
      }
      // return row.warehouseWarrantList[0].wwNo+"~"+ row.warehouseWarrantList[row.warehouseWarrantList.length-1].wwNo+"("+row.warehouseWarrantList.length+")"
    },
    //删除合同或仓单
    deleteRecord(type) {
      if (type == "合同") {
        this.$confirm("此操作删除该合同, 是否继续?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
          .then(() => {
            this.axios
              .delete(ajaxUrl.HOLDWARRANTDELETE, {
                params: {
                  id: this.form.contractId
                }
              })
              .then(res => {
                this.$message({
                  type: "success",
                  message: "删除成功!"
                });
                this.dialogFormVisible = false;
                this.$router.push("/Index");
              })
              .catch(err => {
                console.log(err);
              });
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: "已取消删除"
            });
          });
      }
    },
    //排序
    sortChange(params) {
      if(params.order == 'ascending'){
        this.searchInfo(params.prop, 'asc')
      }else if(params.order == 'descending'){
        this.searchInfo(params.prop, 'desc')
      }else{
        this.searchInfo()
      }
    },
    //获取首页信息
    getIndexInfo() {
      
    }
  },
  components: {
    vSearch,
    handleDialog
  },
};
</script>

<style>
.pagination {
  text-align: center;
}
.dialog-title {
  font-size: 20px;
  margin-bottom: 20px;
  display: block;
}
.buttonGroup {
  text-align: center;
  margin-left: -50px;
}
</style>



