const rules = {
  tradeWay: [{
    required: true,
    message: '请输入交易方式',
    trigger: 'change'
  }],
  closepositionTime: [{
    type: 'date',
    required: true,
    message: '请输入时间',
    trigger: 'change'
  }],
  closepositionPrice: [{
    required: true,
    message: '请输入平仓总价',
    trigger: 'blur'
  }],
  closepositionOptionPrice: [{
    required: true,
    message: '请输入平仓时间期货价格',
    trigger: 'blur'
  }],
  username: [{
    required: true,
    message: "请输入用户名",
    trigger: "blur"
  }],
  password: [{
    required: true,
    message: "请输入密码",
    trigger: "blur"
  }],
  newPwdConfirm: [{
    validator: validatePass,
    trigger: 'blur'
  }],
  customerName: [{
    required: true,
    message: "请输入客户名称",
    trigger: "blur"
  }],
  customerNo: [{
    required: true,
    message: "请输入客户号",
    trigger: "blur" 
  }],
  originalPrice: [{
    required: true,
    message: "请输入仓单原价值",
    trigger: "blur" 
  }],
  contractNo: [{
    required: true,
    message: "请输入合同编号",
    trigger: "blur" 
  }],
  totalLoan: [{
    required: true,
    message: "请输入放款金额",
    trigger: "blur" 
  }],
  tradeEndtime: [{
    required: true,
    message: "请输入合同交易到期日",
    trigger: "blur" 
  }],
  customerPhone: [{
    required: true,
    message: "请输入联系电话",
    trigger: "blur"
  }]
}

function validatePass(rule, value, callback) {
  if (value === '' || !value) {
    callback(new Error('请再次输入密码'));
  } else if (value !== this.modifyPwdData.newPwd) {
    callback(new Error('两次输入密码不一致!'));
  } else {
    callback();
  }
};

export {
  rules
}



